/* Base styles for desktop and larger screens */
.testimonials-container {
    display: flex;
    justify-content: space-between;
    color: white;
    padding: 20px;
    background-color: #1551f6;
    height: 100vh; /* Full screen */
  }
  
  .quote-section {
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding-right: 50px;
  }
  
  .quote-section p {
    font-size: 2.5rem;
    font-weight: bold;
  }
  
  .quote-section span {
    font-size: 1.2rem;
    margin-top: 10px;
  }
  
  .testimonials-section {
    flex: 1;
    overflow-y: auto;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
  }
  
  .testimonial-item {
    margin-bottom: 30px;
  }
  
  .testimonial-item p {
    font-size: 1rem;
    font-weight: lighter;
  }
  
  .testimonial-item h4 {
    font-size: 1.1rem;
    margin: 10px 0 5px 0;
  }
  
  .testimonial-item span {
    font-size: 0.9rem;
    color: white;
     font-family: sans-serif;
  }
  
  /* Responsive styles for tablets (iPad, etc.) */
  @media (max-width: 1024px) {
    .testimonials-container {
      flex-direction: column;
      height: auto;
    }
  
    .quote-section {
      padding-right: 0;
      padding-bottom: 20px;
    }
  
    .quote-section p {
      font-size: 2rem;
      text-align: center;
    }
  
    .quote-section span {
      font-size: 1rem;
      text-align: center;
    }
  
    .testimonials-section {
      padding-left: 10px;
      padding-right: 10px;
    }
  
    .testimonial-item p {
      font-size: 0.95rem;
    }
  
    .testimonial-item h4 {
      font-size: 1rem;
    }
  
    .testimonial-item span {
      font-size: 0.85rem;
    }
  }
  
  /* Responsive styles for mobile devices (iPhone, smaller screens) */
  @media (max-width: 768px) {
    .testimonials-container {
      flex-direction: column;
      height: auto;
    }
  
    .quote-section p {
      font-size: 1.8rem;
      text-align: center;
    }
  
    .quote-section span {
      font-size: 0.9rem;
      text-align: center;
    }
  
    .testimonials-section {
      padding-left: 10px;
      padding-right: 10px;
    }
  
    .testimonial-item p {
      font-size: 0.85rem;
    }
  
    .testimonial-item h4 {
      font-size: 0.95rem;
    }
  
    .testimonial-item span {
      font-size: 0.8rem;
    }
  }
  
  /* Extra styles for very small screens (iPhone SE, small Android devices) */
  @media (max-width: 480px) {
    .quote-section p {
      font-size: 1.5rem;
    }
  
    .testimonial-item p {
      font-size: 0.75rem;
    }
  
    .testimonial-item h4 {
      font-size: 0.85rem;
    }
  
    .testimonial-item span {
      font-size: 0.75rem;
    }
  }
  